	import {
		createRouter,
		createWebHashHistory
	} from 'vue-router'
	import news from '@/views/news/list'
	import cheweiyuyue from '@/views/cheweiyuyue/list'
	import caipinleixing from '@/views/caipinleixing/list'
	import discussmeishixinxi from '@/views/discussmeishixinxi/list'
	import storeup from '@/views/storeup/list'
	import wodeyuyue from '@/views/wodeyuyue/list'
	import users from '@/views/users/list'
	import shangjia from '@/views/shangjia/list'
	import cheweixinxi from '@/views/cheweixinxi/list'
	import discusscantingxinxi from '@/views/discusscantingxinxi/list'
	import cantingxinxi from '@/views/cantingxinxi/list'
	import huodongbaoming from '@/views/huodongbaoming/list'
	import yonghu from '@/views/yonghu/list'
	import systemNotice from '@/views/systemNotice/list'
	import cantingyuyue from '@/views/cantingyuyue/list'
	import meishixinxi from '@/views/meishixinxi/list'
	import dianpuhuodong from '@/views/dianpuhuodong/list'
	import orders from '@/views/orders/list'
	import config from '@/views/config/list'
	import usersCenter from '@/views/users/center'
	import shangjiaRegister from '@/views/shangjia/register'
	import shangjiaCenter from '@/views/shangjia/center'

export const routes = [{
		path: '/login',
		name: 'login',
		component: () => import('../views/login.vue')
	},{
		path: '/',
		name: '',
		component: () => import('../views/index'),
		children: [{
			path: '/',
			name: '',
			component: () => import('../views/HomeView.vue'),
			meta: {
				affix: true
			}
		}, {
			path: '/updatepassword',
			name: '',
			component: () => import('../views/updatepassword.vue')
		}
		
		,{
			path: '/usersCenter',
			name: '',
			component: usersCenter
		}
		,{
			path: '/shangjiaCenter',
			name: '',
			component: shangjiaCenter
		}
		,{
			path: '/news',
			name: '',
			component: news
		}
		,{
			path: '/cheweiyuyue',
			name: '',
			component: cheweiyuyue
		}
		,{
			path: '/caipinleixing',
			name: '',
			component: caipinleixing
		}
		,{
			path: '/discussmeishixinxi',
			name: '-',
			component: discussmeishixinxi
		}
		,{
			path: '/storeup',
			name: '',
			component: storeup
		}
		,{
			path: '/wodeyuyue',
			name: '',
			component: wodeyuyue
		}
		,{
			path: '/users',
			name: '',
			component: users
		}
		,{
			path: '/shangjia',
			name: '',
			component: shangjia
		}
		,{
			path: '/cheweixinxi',
			name: '',
			component: cheweixinxi
		}
		,{
			path: '/discusscantingxinxi',
			name: '-',
			component: discusscantingxinxi
		}
		,{
			path: '/cantingxinxi',
			name: '',
			component: cantingxinxi
		}
		,{
			path: '/huodongbaoming',
			name: '',
			component: huodongbaoming
		}
		,{
			path: '/yonghu',
			name: '',
			component: yonghu
		}
		,{
			path: '/systemNotice',
			name: '',
			component: systemNotice
		}
		,{
			path: '/cantingyuyue',
			name: '',
			component: cantingyuyue
		}
		,{
			path: '/meishixinxi',
			name: '',
			component: meishixinxi
		}
		,{
			path: '/dianpuhuodong',
			name: '',
			component: dianpuhuodong
		}
		,{
			path: '/orders',
			name: '',
			component: orders
		}
		,{
			path: '/config',
			name: '',
			component: config
		}
		]
	},
	{
		path: '/shangjiaRegister',
		name: '',
		component: shangjiaRegister
	},
]

const router = createRouter({
	history: createWebHashHistory(process.env.BASE_URL),
	routes
})

export default router
