<template>
	<div>
		<div class="register_view">
			<el-form :model="registerForm" class="register_form">
				<div class="title_view">{{projectName}}</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.shangjiazhanghao" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.mima" 
						 placeholder=""
						 type="password"
						 />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp" v-model="registerForm.mima2" type="password" placeholder="" />
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.shangjiamingcheng" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<div :style='{}' class="list_file_list">
						<uploads
							action="file/upload" 
							tip=""
							:fileUrls="registerForm.touxiang?registerForm.touxiang:''" 
							@change="touxiangUploadSuccess">
						</uploads>
					</div>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.fuzeren" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.dianhua" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_item">
					<div class="list_label"></div>
					<el-input class="list_inp"
						 v-model="registerForm.shangjiadizhi" 
						 placeholder=""
						 type="text"
						/>
				</div>
				<div class="list_btn">
					<el-button class="register" type="success" @click="handleRegister"></el-button>
					<div class="r-login" @click="close"></div>
				</div>
			</el-form>
		</div>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
		onMounted,
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context?.$project.projectName
	//
	import { useRoute } from 'vue-router';
	const route = useRoute()
	const tableName = ref('shangjia')
	
	//
	const getUUID=()=> {
		return new Date().getTime();
	}
	const registerForm = ref({
         sfsh: '',
	})
	const init=()=>{
	}
    const touxiangUploadSuccess=(fileUrls)=> {
        registerForm.value.touxiang = fileUrls;
    }
	// 
	//
	const handleRegister = () => {
		let url = tableName.value +"/register";
		if((!registerForm.value.shangjiazhanghao)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((!registerForm.value.mima)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.mima!=registerForm.value.mima2){
			context?.$toolUtil.message('','error')
			return false
		}
		if((!registerForm.value.shangjiamingcheng)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(registerForm.value.touxiang!=null){
			registerForm.value.touxiang = registerForm.value.touxiang.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(registerForm.value.dianhua&&(!context?.$toolUtil.isMobile(registerForm.value.dianhua))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		context?.$http({
			url:url,
			method:'post',
			data:registerForm.value
		}).then(res=>{
			context?.$toolUtil.message('','success', obj=>{
				context?.$router.push({
					path: "/login"
				});
			})
		})
	}
	//
	const close = () => {
		context?.$router.push({
			path: "/login"
		});
	}
	init()
	onMounted(()=>{

	})
</script>
