<template>
	<div>
		<div class="center_view edit_form">
			<el-form class="userinfo_form" ref="userinfoFormRef" :model="form">
				<el-row>
					<el-col :span="24">
						<el-form-item label="" prop="shangjiazhanghao">
							<el-input class="list_inp" v-model="user.shangjiazhanghao" readonly placeholder="" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="shangjiamingcheng">
							<el-input class="list_inp" v-model="user.shangjiamingcheng"  placeholder="" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="touxiang">
							<uploads
								action="file/upload" 
								tip=""
								style="width: 100%;text-align: left;"
								:fileUrls="user.touxiang?user.touxiang:''" 
								@change="shangjiatouxiangUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="fuzeren">
							<el-input class="list_inp" v-model="user.fuzeren"  placeholder="" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="dianhua">
							<el-input class="list_inp" v-model="user.dianhua"  placeholder="" clearable />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="shangjiadizhi">
							<el-input class="list_inp" v-model="user.shangjiadizhi"  placeholder="" clearable />
						</el-form-item>
					</el-col>
					<span class="formModel_btn_box">
						<el-button class='confirm_btn' type="primary" @click="onSubmit"></el-button>
					</span>
				</el-row>
			</el-form>
		</div>
	</div>
</template>

<script setup>
	import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/toolUtil";
	import {
		reactive,
		ref,
		getCurrentInstance
	} from 'vue'
	import { useStore } from 'vuex'
	const store = useStore()
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const tableName = ref('shangjia')
	const user = ref({})
	const init = () => {
	}
	const shangjiatouxiangUploadSuccess=(fileUrls)=> {
	    user.value.touxiang = fileUrls;
	}
	const onSubmit = () => {
		if((!user.value.shangjiazhanghao)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((!user.value.mima)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if((!user.value.shangjiamingcheng)){
			context?.$toolUtil.message(``,'error')
			return false
		}
		if(user.value.touxiang!=null){
			user.value.touxiang = user.value.touxiang.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if((user.value.dianhua)&&(!context?.$toolUtil.isMobile(user.value.dianhua))){
			context?.$toolUtil.message(``,'error')
			return false
		}
		store.dispatch('user/update',user.value).then(res=>{
			if(res?.data&&res.data.code==0)context?.$toolUtil.message('','success')
		})

	}
	const getInfo = () => {
		context?.$http({
			url: `${tableName.value}/session`,
			method: 'get'
		}).then(res => {
			user.value = res.data.data
			init()
		})
	}
	getInfo()
</script>

