<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="50%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row >
					<el-col :span="24">
						<el-form-item label="" prop="cheweibianhao">
							<el-input class="list_inp" v-model="form.cheweibianhao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.cheweibianhao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="cheweiweizhi">
							<el-input class="list_inp" v-model="form.cheweiweizhi" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.cheweiweizhi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="yuyueshijian">
							<el-date-picker
								class="list_date"
								v-model="form.yuyueshijian"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:disabled-date="yuyueshijianDisabledDate"
								:readonly="!isAdd||disabledForm.yuyueshijian?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="xiaoshijiage">
							<el-input class="list_inp" v-model.number="form.xiaoshijiage" placeholder=""
                                type="number"
								:readonly="!isAdd||disabledForm.xiaoshijiage?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="ruchangzhuangtai">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.ruchangzhuangtai?true:false"
								v-model="form.ruchangzhuangtai" 
								placeholder=""
								>
								<el-option v-for="(item,index) in ruchangzhuangtaiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="zhanghao">
							<el-input class="list_inp" v-model="form.zhanghao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.zhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="mingcheng">
							<el-input class="list_inp" v-model="form.mingcheng" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.mingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="chepaihao">
							<el-input class="list_inp" v-model="form.chepaihao" placeholder=""
                                type="text"
								:readonly="!isAdd||disabledForm.chepaihao?true:false" />
						</el-form-item>
					</el-col>

				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'cheweiyuyue'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
        cheweibianhao : false,
        cheweiweizhi : false,
        yuyueshijian : false,
        xiaoshijiage : false,
        ruchangzhuangtai : false,
        zhanghao : false,
        mingcheng : false,
        chepaihao : false,
        ispay : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
    
	const rules = ref({
		cheweibianhao: [
		],
		cheweiweizhi: [
		],
		yuyueshijian: [
		],
		xiaoshijiage: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		ruchangzhuangtai: [
		],
		zhanghao: [
		],
		mingcheng: [
		],
		chepaihao: [
		],
		ispay: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	// 
	const yuyueshijianDisabledDate = (date) => {
		return date.getTime() < Date.now() - 8.64e7;
	}
	//
	const ruchangzhuangtaiLists = ref([])

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			cheweibianhao: '',
			cheweiweizhi: '',
			yuyueshijian: '',
			xiaoshijiage: '',
			ruchangzhuangtai: '',
			zhanghao: '',
			mingcheng: '',
			chepaihao: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='cheweibianhao'){
					form.value.cheweibianhao = row[x];
					disabledForm.value.cheweibianhao = true;
					continue;
				}
				if(x=='cheweiweizhi'){
					form.value.cheweiweizhi = row[x];
					disabledForm.value.cheweiweizhi = true;
					continue;
				}
				if(x=='yuyueshijian'){
					form.value.yuyueshijian = row[x];
					disabledForm.value.yuyueshijian = true;
					continue;
				}
				if(x=='xiaoshijiage'){
					form.value.xiaoshijiage = row[x];
					disabledForm.value.xiaoshijiage = true;
					continue;
				}
				if(x=='ruchangzhuangtai'){
					form.value.ruchangzhuangtai = row[x];
					disabledForm.value.ruchangzhuangtai = true;
					continue;
				}
				if(x=='zhanghao'){
					form.value.zhanghao = row[x];
					disabledForm.value.zhanghao = true;
					continue;
				}
				if(x=='mingcheng'){
					form.value.mingcheng = row[x];
					disabledForm.value.mingcheng = true;
					continue;
				}
				if(x=='chepaihao'){
					form.value.chepaihao = row[x];
					disabledForm.value.chepaihao = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.ruchangzhuangtai=''
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(context?.$toolUtil.storageGet("role")!="" && !context?.$toolUtil.storageGet("isAdmin")) {
				disabledForm.value.ruchangzhuangtai = true;
			}
			if(json.hasOwnProperty('zhanghao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.zhanghao = json.zhanghao
				disabledForm.value.zhanghao = true;
			}
			if(json.hasOwnProperty('mingcheng')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.mingcheng = json.mingcheng
				disabledForm.value.mingcheng = true;
			}
			if(json.hasOwnProperty('chepaihao')&& context?.$toolUtil.storageGet("role")!=""){
				form.value.chepaihao = json.chepaihao
				disabledForm.value.chepaihao = true;
			}
		})
		ruchangzhuangtaiLists.value = ",".split(',')
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}else{
                context.$message.error('')
            }
		})
	}
	//
	const changeCrossData = async (row)=>{
        if(type.value == 'cross'){
            await context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				//
				.list_date {
				}
				// 
				.list_sel {
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
						}
					}
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
